<template>
  <div class="container">
    <!-- 搜索组件一级路由   返回上一个页面-->
    <van-nav-bar left-arrow title="搜索中心" @click-left="$router.back()"></van-nav-bar>
    <!-- 导航 -->
    <van-search  placeholder="请输入搜索关键词" shape="round" v-model.trim="q"   @search="onSearch"/>
    <van-cell-group class="suggest-box" v-if="q" >
      <van-cell icon="search" v-for="item in suggestionList" :key="item" @click="onSearchLianXiang(item)">
        <span>{{item}}</span>
      </van-cell>
    </van-cell-group>
    <div class="history-box" v-else-if="historyList.length">
      <div class="head">
        <span>历史记录</span>
        <van-icon name="delete" @click="clear"></van-icon>
      </div>
      <!-- 点击历史记录跳到搜索页面 -->
      <van-cell-group v-for="(item,index) in historyList" :key="item" @click="toResult(item)">
        <van-cell>
          <a class="word_btn">{{item}}</a>
          <van-icon class="close_btn" slot="right-icon" name="cross" @click.stop="delHistory(index)" />
        </van-cell>
      </van-cell-group>
    </div>
  </div>
</template>

<script>
import { suggestion } from '@/api/articles'
const historyKey = 'hm-toutiao-01-history'
export default {
  name: 'search',
  data () {
    return {
      q: '',
      historyList: [],
      suggestionList: []
    }
  },
  created () {
    this.gethistoryList()
    // ["马云","刘强东"]
  },
  watch: {
    q () {
      clearTimeout(this.timer)
      this.timer = setTimeout(async () => {
        if (!this.q) {
          this.suggestionList = []
          return
        }
        let data = await suggestion({ q: this.q })
        this.suggestionList = data.options
      }, 300)
    }
  },
  methods: {
    gethistoryList () {
      // 去本地拿数据
      this.historyList = JSON.parse(localStorage.getItem(historyKey) || '[]')
    },
    delHistory (hindex) {
      // 按照下标删除历史记录
      // 删除this.historyList
      this.historyList.splice(hindex, 1)
      // 再给他存回缓存
      localStorage.setItem(historyKey, JSON.stringify(this.historyList))
    },
    toResult (text) {
      this.$router.push({ path: '/search/result', query: { q: text } })
    },
    clear () {
      this.historyList = []
      localStorage.setItem(historyKey, '[]')
    },
    onSearch () {
      // 还要把它存入历史记录里
      // 得去重
      let set = new Set(this.historyList)
      set.add(this.q)
      this.historyList = Array.from(set)
      localStorage.setItem(historyKey, JSON.stringify(this.historyList))
      this.$router.push({ path: '/search/result', query: { q: this.q } })
    },
    onSearchLianXiang (text) {
      let set = new Set(this.historyList)
      set.add(text)
      this.historyList = Array.from(set)
      localStorage.setItem(historyKey, JSON.stringify(this.historyList))
      this.$router.push({ path: '/search/result', query: { q: text } })
    }
  }
}
</script>

<style lang='less' scoped>
.history-box {
  padding: 0 20px;
  .head{
    line-height: 36px;
    color: #999;
    .van-icon{
      font-size: 16px;
      float: right;
      margin-top: 10px;;
    }
  }
  .van-cell{
    padding: 10px 0;
  }
  .word_btn{
    color:#3296fa;
  }
  .close_btn{
    margin-top:5px;
    color: #999;
  }
}
.suggest-box{
  /deep/ .van-cell{
    padding: 10px 20px;
    color: #999;
    p{
      span{
        color: red;
      }
    }
  }
}
</style>
